.footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.0em;
    background-color: rgba(53, 74, 95, 1);
    max-height: 48px; /* This keeps the waves to the top of the foot connected */
}

.footer p {
    opacity: 0.75;
    text-align: center;
    margin: 0;
}

/* Animation */
#background-container {
    position: absolute;
    left: 0;
    bottom: 44px; /* This keeps the waves to the top of the foot connected */
    width: 100%;
    z-index: -1;
}

.parallax>use {
    animation: move-forever 25s cubic-bezier(.55, .5, .45, .5) infinite;
}

.parallax>use:nth-child(1) {
    animation-delay: -2s;
    animation-duration: 7s;
}

.parallax>use:nth-child(2) {
    animation-delay: -3s;
    animation-duration: 10s;
}

.parallax>use:nth-child(3) {
    animation-delay: -4s;
    animation-duration: 13s;
}

.parallax>use:nth-child(4) {
    animation-delay: -5s;
    animation-duration: 20s;
}

@keyframes move-forever {
    0% {
        transform: translate3d(-90px, 0, 0);
    }
    100% {
        transform: translate3d(85px, 0, 0);
    }
}